// 三角形
.ui-triangle{
	position:absolute;
	left:0;
	top:0px;
	width:0px;
	height:0px;
	font-size:0px;
	line-height:0px;
	border-style:solid;
	
	/* 以下是构造三角形代码 */
	-webkit-transition:-webkit-transform .2s ease-in;/* chrome & safari */
	-moz-transition:-webkit-transform .2s ease-in;/* firefox */
	-o-transition:-webkit-transform .2s ease-in;/* Opera */
	transition:-webkit-transform .2s ease-in;/* ie */

}

// 白色
.ui-triangle-top-max-white{
	border-width:0px 7px 7px 7px;/* 大小 */
	border-color: transparent transparent #ffffff transparent;/* 颜色 */
	_border-color: #000 #000 #fff #000;/* 颜色 */
	_filter:chroma(color=black);
}


