//消息通知
.msg_info{
    width: 100%;
    background: #4092f1;
    border-radius: 2px;
    padding: 0px 0px 12px 0px;
    .list{
        padding: 0px 15px 0 15px;
    }
    li{
        padding-top: 12px;
        color: #fff;
        line-height: 24px;
        position: relative;
        padding-right: 20px;
        a{
            color: #fff;
            text-decoration: underline;
            &:hover{
                text-decoration: none;
            }
        }
    }
    .close{
        display: block;
        background: url(../../images/close-12x12.png) no-repeat;
        width: 12px;
        height: 12px; 
        display: block;
        position: absolute;
        right: -3px;
        top: 12px;
    }

}

